html {
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/level_2_prototype/static/media/background-img.79d68011.png);
  background-size: 5%;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  position: absolute;
  min-height: 100vh;
  width: 100%;
  background: -webkit-linear-gradient(top, rgba(0,0,0,0.5) 0%, rgba(0, 0, 0, 0.0001) 49%, rgba(0, 0, 0, 1) 100%);
  background: linear-gradient(180deg, rgba(0,0,0,0.5) 0%, rgba(0, 0, 0, 0.0001) 49%, rgba(0, 0, 0, 1) 100%);
}

.content {
  margin: 0;
}

.header-text {
  font-family: 'Exo 2';
  font-weight: 250;
  font-size: 36px;
  text-align: center;
  color: #FFFFFF;
  text-shadow: 0px 1px 1px #000000;
  margin-top: 10%;
}
.itemView {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.productUnit {
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.productInfo, .endProductInfo {
    margin: 14px 0;
    font-size: 13px;
}

.productInfo {
    color: #fff;
}

.endProductInfo {
    color: #ff6;
}

.product {
    border-radius: 12px;
    cursor: pointer;
    width: 320px;
    height: 480px;
    margin: 0 40px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
}

.productText {
    width: 211px;
    font-family: Trebuchet MS;
    text-align: left;
}

.slogan{
    color: #666666;
}

.productText h2, .productText h3 {
    font-weight: bold;
    line-height: normal;
    margin: 0;
}

.productText h2 {
    font-size: 48px;
}

.productText h3 {
    font-size: 24px;
    text-align: left;
}

.weightUnit {
    line-height: 0px;
    font-size: 21px;
    margin: 0px;
}

.weightNumber {
    line-height: 15px;
    font-weight: normal;
    font-size: 42px;
    margin: 0; 
}

.weightCircle {
    border-radius: 50%;
    background: #1698D9;
    height: 80px;
    width: 80px;
    position: relative;
    left: 108px;
    bottom: 12px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #FFFFFF;
}

.notSelected:hover .weightCircle {
    background: #2EA8E6;
}

.buy {
    cursor: pointer;
    padding: 2px 0px;
    width: 30px;
    color: #1698D9;
    border-bottom: 1px dashed #1698D9;
}

.notSelected {
    background: url(data:image/png;base64,) no-repeat,url(/level_2_prototype/static/media/Cat.7ce02eda.png) 0% 100% no-repeat, url(data:image/png;base64,) no-repeat;
}
.notSelected:hover {
    background: url(data:image/png;base64,) no-repeat,url(/level_2_prototype/static/media/Cat.7ce02eda.png) 0% 100% no-repeat, url(data:image/png;base64,) no-repeat;
}

.Selected {
    background: url(data:image/png;base64,) no-repeat,url(/level_2_prototype/static/media/Cat.7ce02eda.png) 0% 100% no-repeat, url(data:image/png;base64,) no-repeat;
}
.Selected:hover {
    background: url(data:image/png;base64,) no-repeat,url(/level_2_prototype/static/media/Cat.7ce02eda.png) 0% 100% no-repeat, url(data:image/png;base64,) no-repeat;
}

.Selected .weightCircle {
    background: #D91667;
}

.Selected:hover .weightCircle {
    background: #E52E7A;
}

.sloganHover {
    color: #E62E7A;
}


.Selected:hover .slogan, .sloganHover {
    display: none;
}

.slogan, .Selected:hover .sloganHover {
    display: block;
    max-height: 19px;
    opacity: 1;
    margin-top: 20px;
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: bold;
    white-space: nowrap;
}

.endProduct {
    background: url(data:image/png;base64,) no-repeat,url(/level_2_prototype/static/media/Cat.7ce02eda.png) 0% 100% no-repeat, url(data:image/png;base64,) no-repeat;
    -webkit-filter: grayscale(.75);
            filter: grayscale(.75);
    color: #B3B3B3;
    cursor:default;
}

.endProduct .weightCircle {
    background: #B3B3B3;
}


.productPromo {
    margin-top: 17px;
    font-size: 14px;
}

.productPromo {
    color: #666666;
}
